.card {
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);  
}





.Card {
    
    padding: 12px;
    
    color: white;
    
    background-color: #4b4b4b;

}


.Card:hover {
    
    transform: translateY(4px);
    background-color: rgb(5, 5, 5);
    
}